import React, {useState} from 'react';
import s from './App.module.css';
import { useDrag } from '@use-gesture/react';
import { useSpring, animated } from '@react-spring/web'


export const AppItem = () => {
    return (
        <div className={s.apps}>

        </div>
    )

}

export const App = () => {

    const [{ x, y }, api] = useSpring(() => ({ x: 0, y: 0 }))

    const [state, setState] = useState<number>(0);

    const bind = useDrag(({ active, movement: [mx, my], cancel }) => {
        if (mx < -54 || my < -54){
            if(mx < -49 && my < 49){
                cancel();
                console.log('App is closed!');
                setState(state + 1);
            }
            cancel();
        }
        api.start({ x: active ? mx : 0, y: active ? my : 0, immediate: active })
    })


    return (
        <div className={s.app_test}>
            <div className={s.number_test}>{state}</div>
            <animated.div {...bind()} style={{ x,  y }} className={s.apps}/>
        </div>

    )
};